.collapse-wrapper {
  width: 50%;
  height: 250px;
  overflow-y: scroll;
}
.collapse-wrapper ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.collapse-wrapper li {
  width: 100%;
  border: 1px solid #e8e8e8;
  border-bottom: none;
}
.collapse-wrapper li:hover {
  cursor: pointer;
}
.collapse-wrapper li .title {
  padding-left: 24px;
  height: 37px;
  line-height: 37px;
}
.collapse-wrapper li .content {
  border-top: 1px solid #e8e8e8;
  transition: height 0.3s;
  line-height: 24px;
  height: 0;
  visibility: hidden;
}
.collapse-wrapper li .content.open {
  height: 45px;
  padding: 10px 24px 10px;
  visibility: visible;
}
