.transfer-wrapper {
  width: 800px;
  height: 300px;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", SimSun, sans-serif;
}
.transfer-left {
  width: 50%;
  height: 100%;
  float: left;
  border: 1px solid #e8e8e8;
  overflow-y: scroll;
}
.transfer-left ul {
  padding: 0;
}
.transfer-left ul li {
  padding: 0 10px 0;
  list-style: none;
  height: 35px;
  line-height: 35px;
}
.transfer-left ul li:hover {
  background-color: orange;
}
.transfer-right {
  width: 50%;
  height: 100%;
  float: left;
  border: 1px solid #e8e8e8;
  padding: 10px;
}
.transfer-right .label-item {
  display: inline-block;
  margin: 0 10px 5px 0;
  padding: 0 10px 0;
  height: 20px;
  line-height: 20px;
  border-radius: 5px;
  background-color: #87d068;
  color: #fff;
  font-weight: 400;
  font-size: 12px;
}
.transfer-right .label-item i {
  margin-left: 3px;
  cursor: pointer;
}
.transfer-right .label-item i:hover {
  color: #d3d3d3;
}
