* {
  box-sizing: border-box;
}
#excel-demo {
  width: 954px;
  height: 500px;
  border: 1px solid #e8e8e8;
  overflow-x: auto;
}
#excel-demo .big-wrapper {
  width: 1500px;
  position: relative;
  padding: 20px;
  height: 100%;
  cursor: pointer;
}
#excel-demo .square {
  width: 100px;
  height: 100px;
  position: relative;
  display: inline-block;
  text-align: center;
  line-height: 100px;
  margin-right: 20px;
  margin-bottom: 40px;
  border: 1px solid #e8e8e8;
  transition: all 0.5s;
}
#excel-demo .square:hover {
  box-shadow: 0 0 5px 1px #c54bd0;
}
#excel-demo .square:nth-last-child(1) {
  margin-right: 0;
}
#excel-demo .square .tree-top-line {
  width: 120px;
  height: 22px;
  position: absolute;
  display: inline-block;
  top: -22px;
  left: 50px;
  border-top: 1px solid #e8e8e8;
  border-left: 1px solid #e8e8e8;
}
#excel-demo .square .tree-bottom-line {
  width: 1px;
  height: 20px;
  position: absolute;
  display: inline-block;
  bottom: -20px;
  left: 50%;
  background-color: #e8e8e8;
}
#excel-demo .square .del-border-top {
  border-top: none;
}
#excel-demo .square .open-icon {
  width: 15px;
  height: 15px;
  display: inline-block;
  position: absolute;
  line-height: 13px;
  bottom: -7.5px;
  color: #ffffff;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 7.5px;
  background-color: #1BDAE9;
}
#excel-demo .square .open-icon i {
  margin-left: 1px;
  font-style: normal;
}
#excel-demo .square .none {
  display: none;
}
#excel-demo .square .name {
  font-size: 14px;
  color: #333333;
}
#excel-demo .level-0-wrapper {
  position: absolute;
  left: 500px;
}
#excel-demo .level-1-wrapper {
  position: absolute;
  left: 500px;
  top: 160px;
}
#excel-demo .level-2-wrapper {
  position: absolute;
  left: 500px;
  top: 300px;
}
