.drag-sort-wrapper ul,
.drag-sort-wrapper li {
  list-style: none;
  padding: 0;
  margin: 0;
  cursor: move;
}
.drag-sort-wrapper .data-sort-abstract ul {
  width: 50px;
  height: 150px;
  margin-bottom: 10px;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  position: relative;
}
.drag-sort-wrapper .data-sort-abstract ul li {
  border: 1px solid #e8e8e8;
  position: relative;
}
.drag-sort-wrapper .data-display-content ul {
  width: 50px;
}
.drag-sort-wrapper .data-display-content li {
  border: 1px solid #df3b3b;
}
.drag-sort-wrapper .hold {
  border: 1px dashed orange !important;
  box-sizing: border-box;
}
