.circle-progress-bar-wrapper {
  width: 50px;
  height: 50px;
  position: relative;
  display: inline-block;
  margin-left: 20px;
}
.circle-progress-bar-wrapper .left-circle-wrapper {
  width: 50%;
  height: 100%;
  position: absolute;
  left: 0;
  overflow: hidden;
}
.circle-progress-bar-wrapper .left-circle-wrapper .left-circle {
  position: absolute;
  left: 0;
  border-top: 25px solid #e5e90d;
  border-left: 25px solid #e5e90d;
  border-right: 25px solid #fff;
  border-bottom: 25px solid #fff;
  border-radius: 50%;
  transform: rotate(-225deg);
}
.circle-progress-bar-wrapper .process-num {
  width: 20px;
  height: 20px;
  border: 20px solid #fff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  z-index: 1;
  border-radius: 50%;
  text-align: center;
  color: #cf1616;
}
.circle-progress-bar-wrapper .process-num span {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.circle-progress-bar-wrapper .right-circle-wrapper {
  width: 50%;
  height: 100%;
  position: absolute;
  right: 0;
  overflow: hidden;
}
.circle-progress-bar-wrapper .right-circle-wrapper .right-circle {
  position: absolute;
  right: 0;
  border-top: 25px solid #fff;
  border-left: 25px solid #fff;
  border-right: 25px solid #0de944;
  border-bottom: 25px solid #0de944;
  border-radius: 50%;
  transform: rotate(-225deg);
}
