.structure-tree-wrapper {
  width: 800px;
  height: 300px;
}
.structure-tree-wrapper ul {
  padding: 0;
}
.structure-tree-wrapper ul li {
  height: 35px;
  line-height: 35px;
  padding: 0 10px 0;
  transition: all 0.5s;
}
.structure-tree-wrapper ul li:hover {
  background-color: #87d068;
}
.structure-tree-wrapper .left-tree {
  width: 50%;
  height: 100%;
  float: left;
  border: 1px solid #e8e8e8;
}
.structure-tree-wrapper .left-tree ul {
  height: 300px;
  overflow-y: scroll;
}
.structure-tree-wrapper .right-tree {
  width: 50%;
  height: 100%;
  float: left;
  border: 1px solid #e8e8e8;
}
.structure-tree-wrapper .right-tree ul {
  height: 300px;
  overflow-y: scroll;
}
