.boxshadow-btn {
  width: 70px;
  height: 30px;
  outline: none;
  background-color: orange;
  border: none;
  text-align: center;
  line-height: 30px;
  padding: 0;
  color: #fff;
  margin-left: 10px;
}
.boxshadow-btn:hover {
  animation: pulse 1s;
  box-shadow: 0 0 0 2em rgba(245, 189, 71, 0);
}
@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 #f5bd47;
  }
}
