* {
  box-sizing: border-box;
}
.tree-wrapper {
  width: 100%;
  height: 300px;
  border: 1px solid #e8e8e8;
}
.tree-wrapper .left {
  width: 50%;
  height: 100%;
  border-right: 1px solid #e8e8e8;
  float: left;
}
.tree-wrapper .left ul {
  width: 100%;
  height: 100%;
  padding: 0;
  overflow-y: auto;
}
.tree-wrapper .left ul li {
  width: 100%;
  height: 30px;
  list-style: none;
  line-height: 30px;
  background-color: #fff;
  transition: all 0.5s;
  color: #28bcff;
}
.tree-wrapper .left ul li i {
  text-indent: 0;
  transition: transform 0.5s;
}
.tree-wrapper .left ul li span {
  padding-left: 10px;
}
.tree-wrapper .left ul li:hover {
  background-color: #ea8266;
  color: #fff;
}
.tree-wrapper .level0 {
  text-indent: 5px;
}
.tree-wrapper .level1 {
  text-indent: 15px;
}
.tree-wrapper .level2 {
  text-indent: 40px;
}
.tree-wrapper .down {
  transform: rotateZ(90deg);
}
.tree-wrapper .table-wrapper {
  overflow-y: auto;
}
.tree-wrapper .right {
  width: 50%;
  height: 100%;
  float: left;
}
